import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';
import Education from './education';
import Experience from './experience';
import Skills from './Skills';

class Resume extends Component{
	render(){
		return(
			<div>
				<Grid>
					<Cell col={4}>
						<div style={{textAlign:"center"}}>
						<img 
						src={require('./avatar-4.jpeg')}
						alt="avatar"
						style={{height:"200px", borderRadius:"50%"}}
						/>
						</div>
						<div className="col-right">
						<h2 style={{paddingTop:"20px"}}>Vaishakh KP</h2>
						<h4 style={{color:"Gray"}}>Programmer</h4>
						<hr style={{borderTop:"3px solid #833fb2", width:"50%"}} />
						<p style={{fontWeight:"bold"}}>I am a full stack web Developer, I really love to learn new technoogies and implement them in the best way how the user needs.</p>
						<hr style={{borderTop:"3px solid #833fb2", width:"50%"}} />
						<h5>Address</h5>
						<p>Keezhattupurath Illam, Naderi PO, Koyilandy VIA, Calicut, Kerala-673620</p>
						<h5>Phone</h5>
						<p>(+91) 9400993041</p>
						<h5>Email</h5>
						<p>kpvaisakh@gmail.com</p>
						<hr style={{borderTop:"3px solid #833fb2", width:"50%"}} />
						</div>
					</Cell>
					<Cell className="resume-right-col" col={8}>
						<h2>Education</h2>

						<Education 
						startYear={2010}
						endYear={2014}
						collegeName={"Gnanamani College of Technology"}
						collegeDescription={"An Anna University Chennai affiliated college situated in Salem Tamilnadu, completed my Bachelor of Engineering graduation in computer science and engineering. "}
						/>

						<Education 
						startYear={2014}
						endYear={2015}
						collegeName={"NIIT"}
						collegeDescription={"Completed diploma in core java and oracle 10g"}
						/>

						<hr style={{borderTop:"3px solid #e22947"}} />
						<h2>Experience</h2>

						<Experience 
							startYear={2016}
							endYear={2018}
							jobName={"Digital interaction Excicutive at [24]7 "}
							jobDescription={"I started my career as a chat executive in sales and customer support for sears and Kmart."}
						/>

						<Experience 
							startYear={2018}
							endYear={2018}

							jobName={"Tranee engineer at Innovatia India"}
							jobDescription={"Created prototypes for Chat-bot, Issue-search-engine inside release notes and got the chance to improve my skills"}
						/>

						<Experience 
							startYear={2019}
							jobName={"Software Developer at Innovatia India"}
							jobDescription={"Full stack web developement with automation in Technical writing department."}
						/>
						<hr style={{borderTop:"3px solid #e22947"}} />
						<h2>Skills</h2>

						<Skills 
							skills={"JavaScript"}
							progress={80}
						/>

						<Skills 
							skills={"HTML/CSS"}
							progress={80}
						/>

						<Skills 
							skills={"PYTHON/DJANGO"}
							progress={70}
						/>

						<Skills 
							skills={"REACT.JS"}
							progress={70}
						/>


					</Cell>
				</Grid>
			</div>
		)
	}
}
export default Resume;